/*Resets*/
body {
  font-family: "Poppins", Arial, Helvetica, sans-serif;
}

html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font-size: 16;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.entry-footer {
  background-color: rgb(255, 255, 255);
}

footer {
  background-color: rgb(234, 234, 234);
  padding: 0;
  margin-top: auto;
}
footer p {
  margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6 {
  display: block;
  color: rgb(2, 48, 71);
  margin-bottom: 10px;
}

h1 {
  font-size: clamp(16px, 6vw, 48px);
  line-height: 1.4;
}

h2 {
  font-size: clamp(16px, 6vw, 40px);
  line-height: 1.4;
}

h3 {
  font-size: clamp(16px, 6vw, 30px);
  line-height: 1.4;
}
h3 a {
  text-decoration: none;
}
h3 a:hover {
  text-decoration: underline;
}

h4 {
  font-size: clamp(16px, 6vw, 28px);
  line-height: 1.4;
}

h5 {
  font-size: clamp(16px, 6vw, 26px);
  line-height: 1.4;
}

h6 {
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.4;
}

main p {
  margin-bottom: 0.875em;
  line-height: 2rem;
  font-size: clamp(16px, 3vw, 16px);
  line-height: 1.8;
}

main h1 {
  margin-bottom: 20px;
}

section h1 {
  margin-bottom: 40px;
}

main a {
  color: #346aff;
  text-decoration: none;
  padding: 0 2px;
}
main a:hover {
  color: #023047;
  text-decoration: underline;
}

main ul li {
  padding: 5px 0;
}
main ul li a {
  color: #346aff;
}
main ul li a:hover {
  color: #023047;
}

.inline-pills {
  padding: 0;
  margin: 10px 10px 10px 0;
}
.inline-pills li {
  display: inline-block;
  padding: 12px 10px;
  border-radius: 10px;
  background-color: rgb(2, 48, 71);
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-transform: uppercase;
  margin: 5px 0;
}
@media (min-width: 992px) {
  .inline-pills li {
    margin: 5px 10px 5px 0;
  }
}

table {
  margin: 0.3125em 0;
}
table th {
  background-color: rgb(234, 234, 234);
}
table th, table td {
  padding: 0.3125em;
  border: 1px solid rgb(234, 234, 234);
}

address {
  display: block;
  font-style: italic;
  line-height: 1.4rem;
  margin-bottom: 10px;
}

main ol, main ul {
  margin: 10px 12px;
}
main ol li li, main ul li li {
  padding-left: 14px;
}

main ol li {
  line-height: 1.8rem;
}
main ol li li {
  line-height: 1.8rem;
}

main ul.inline-list {
  padding-left: 0;
  margin-left: 0;
}
main ul.inline-list li {
  display: inline-block;
  padding: 6px 0;
  text-transform: uppercase;
  text-decoration: underline;
  margin: 5px 0;
}

code {
  color: crimson;
  background-color: #f1f1f1;
  padding: 2px;
  font-size: 105%;
}

pre {
  display: block;
  font-family: monospace;
  white-space: pre;
  margin: 2em 0;
  background-color: rgb(234, 234, 234);
  border-left: 5px solid rgb(2, 48, 71);
  position: relative;
  overflow: scroll;
  padding: 20px;
}

q {
  color: gray;
  font-style: italic;
}

dl {
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.6;
}

dt {
  display: block;
  background-color: rgb(234, 234, 234);
  padding: 10px;
  font-weight: bold;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.6;
}

dd {
  padding: 10px 20px;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.6;
}

blockquote {
  background-color: rgb(2, 48, 71);
  border-left: 5px solid #354a95;
  color: rgb(255, 255, 255);
  padding: 10px;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.4;
}

cite {
  background-color: rgb(234, 234, 234);
  padding: 10px 20px;
  margin-left: 10px;
  font-style: italic;
}

.subheading {
  font-weight: 100;
  font-size: 1.2rem;
  line-height: 1.6rem;
  margin: 0 0 60px 0;
  padding: 0 10px;
}

.container {
  display: block;
  padding: 40px;
}
@media (min-width: 768px) {
  .container {
    display: flex;
    flex-wrap: wrap;
    padding: 60px 40px;
    margin: 0 auto;
    text-align: left;
    width: 100%;
    max-width: 90vw;
  }
}
@media (min-width: 992px) {
  .container {
    flex-wrap: unset;
  }
}

.wrap {
  flex-wrap: wrap;
}

.project-container {
  display: block;
  width: 100%;
  max-width: 1140px;
  padding: 0 40px 80px 40px;
}
@media (min-width: 1200px) {
  .project-container {
    display: flex;
    align-items: flex-start;
    margin: auto;
    flex-wrap: wrap;
    padding: 0 0 80px 0;
  }
}
.project-container .project {
  padding: 40px 0;
}
@media (min-width: 768px) {
  .project-container .project {
    padding: 40px 20px 0 20px;
  }
}
@media (min-width: 1200px) {
  .project-container .project {
    flex: 1 0 50%;
    margin-bottom: auto;
  }
}
.project-container .project img {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.post-heading img {
  width: 100%;
  height: 400px;
  margin: 30px 0 80px 0;
}

.post-container {
  padding: 40px 0;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.post-content {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  display: block;
}

img {
  margin-bottom: 40px;
  width: 100%;
  height: auto;
}

@media (min-width: 768px) {
  .section-contents {
    display: flex;
    margin: auto;
  }
}
@media (min-width: 992px) {
  .section-contents {
    flex-wrap: unset;
    margin: 0 auto;
    width: 72%;
  }
}
.section-contents h1 {
  margin-bottom: 20px;
}
.section-contents .attachment-post-thumbnail {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .section-contents .attachment-post-thumbnail {
    width: 100%;
  }
}
@media (min-width: 992px) {
  .section-contents .attachment-post-thumbnail {
    width: 100%;
  }
}
.section-contents .col {
  width: 100%;
}
@media (min-width: 768px) {
  .section-contents .col {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .section-contents .col {
    width: 50%;
  }
}

.col {
  width: auto;
  display: inline-block;
  padding: 0 10px;
  vertical-align: top;
}

@media (min-width: 768px) {
  .col-50 {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .col-75 {
    width: 75%;
  }
}
.row {
  padding: 40px 0 20px 0;
}
@media (min-width: 768px) {
  .row {
    padding: 40px 0;
  }
}
@media (min-width: 992px) {
  .row {
    padding: 0;
  }
}

.flexstart {
  justify-content: flex-start;
}

.flexend {
  justify-content: flex-end;
}

.center {
  justify-content: center;
}

.spacearound {
  justify-content: space-around;
}

.spacebetween {
  justify-content: space-between;
}

.items-center {
  align-items: center;
}

.self-center {
  align-self: center;
}

.wrap {
  flex-wrap: wrap;
}

.text-center {
  text-align: center;
}

.grey-background {
  background-color: rgb(234, 234, 234);
  color: #333333;
}

.dark-background {
  background-color: rgb(2, 48, 71);
}

.blue-background {
  background-color: #354a95;
}

.orange-background {
  background-color: rgb(255, 183, 3);
}

.light-text {
  color: rgb(255, 255, 255);
}

.col-padding-20 {
  padding: 20px;
}

.col-spacing-5 {
  margin: 10px;
}

.marb-spacing-40 {
  margin: 0 auto 40px auto;
}

.mart-spacing-100 {
  margin-top: 100px;
}

.rounded-border {
  border-radius: 15px;
}

@media (min-width: 768px) {
  .padding-right-60 {
    padding-right: 60px;
  }
}
.section-spacing {
  padding: 40px;
}
@media (min-width: 768px) {
  .section-spacing {
    padding: 60px 0;
  }
}
@media (min-width: 992px) {
  .section-spacing {
    padding: 80px 0;
  }
}

.contact-section {
  display: block;
  text-align: center;
  padding: 40px 20px;
}
.contact-section h3 {
  margin-bottom: 20px;
  line-height: 1.4em;
}
@media (min-width: 768px) {
  .contact-section {
    padding: 40px 80px;
  }
}
@media (min-width: 1200px) {
  .contact-section {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    justify-content: center;
    align-items: center;
    height: 40vh;
    column-gap: 30px;
    text-align: left;
  }
}

footer.page-footer {
  display: flex;
  flex-wrap: wrap;
  height: 10vh;
  align-items: center;
  justify-content: center;
}

/*Layout Styles*/
.page-banner {
  height: auto;
  background-color: rgb(234, 234, 234);
  display: block;
  height: auto;
  padding: 80px 40px;
  text-align: center;
}
@media (min-width: 1200px) {
  .page-banner {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    justify-content: center;
    justify-items: center;
    width: 100%;
    max-width: 1440px;
    height: 50vh;
    padding: auto 40px;
  }
}
.page-banner h1 {
  font-size: clamp(16px, 6vw, 48px);
  line-height: 1.4;
  margin: auto auto 10px auto;
}
.page-banner p {
  line-height: 1.4;
  margin-bottom: 10px;
}
.page-banner .post-edit-link {
  display: block;
  clear: left;
  padding: 12px 10px 12px 0;
}
@media (min-width: 1200px) {
  .page-banner .post-edit-link {
    text-align: center;
  }
}

.navigation {
  width: 100%;
  margin: auto;
  max-width: 1440px;
}
.navigation .nav-links {
  display: inline-flex;
  flex-wrap: unset;
  min-width: 100%;
}
.navigation .nav-links .nav-next, .navigation .nav-links .nav-previous {
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}
.navigation .nav-links .nav-next a, .navigation .nav-links .nav-previous a {
  padding: 6px 0;
  font-size: clamp(16px, 5vw, 24px);
  line-height: 1.4;
  font-size: clamp(16px, 6vw, 23px);
  line-height: 1.4;
  color: rgb(255, 255, 255);
}
.navigation .nav-links .nav-next a:hover, .navigation .nav-links .nav-previous a:hover {
  color: rgb(255, 255, 255);
}

.banner-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: auto auto 5px auto;
  text-align: left;
  width: 100%;
  max-width: 93vw;
}
@media (min-width: 992px) {
  .banner-container {
    max-width: 92vw;
  }
}
@media (min-width: 1200px) {
  .banner-container {
    max-width: 88vw;
  }
}

.logo {
  color: rgb(2, 48, 71);
  font-weight: 800;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.5px;
  font-size: 1.125em;
  transition: 1s;
}
.logo:hover {
  text-decoration: underline;
  color: #354a95;
}

.menu-social-links {
  display: none;
}

.menu {
  margin: auto;
  width: 100%;
  display: inline-flex;
  height: auto;
}

.menu > li {
  margin: 0;
  overflow: hidden;
  width: auto;
}
.menu > li a {
  color: rgb(2, 48, 71);
  text-decoration: none;
  font-size: 1.125em;
  padding: 1em;
  margin: auto 0 auto 0.1em;
  display: block;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: 0.5s;
}
.menu > li a:hover {
  color: rgb(255, 255, 255);
  background-color: rgb(2, 48, 71);
}

.menu-button-container {
  display: none;
  height: auto;
  width: auto;
  cursor: pointer;
  margin: auto;
  height: 10vh;
  font-size: 1em;
}

#menu-toggle {
  display: none;
  width: auto;
  margin: auto;
}

.menu-button,
.menu-button::before,
.menu-button::after {
  display: inline-flex;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 2px;
  z-index: 10000;
  margin: auto 0;
  height: auto;
}

#menu-toggle + .menu-button-container .menu-button::before {
  font-family: "Font Awesome 6 Pro";
  content: "\f0c9";
  font-size: 2em;
}

#menu-toggle:checked + .menu-button-container .menu-button::before {
  transform: rotate(-405deg);
  content: "";
}

#menu-toggle:checked + .menu-button-container .menu-button {
  background: rgba(255, 255, 255, 0);
  font-family: "Font Awesome 6 Pro";
  content: "\f00d";
  font-size: 2em;
  width: auto;
  margin: 0;
  justify-self: end;
  height: auto;
}

#menu-toggle:checked + .menu-button-container .menu-button::after {
  transform: rotate(0);
  font-family: "Font Awesome 6 Pro";
  content: "\f00d";
}

@media (max-width: 700px) {
  .banner-container {
    max-width: 90vw;
    flex-wrap: wrap;
    background-color: rgb(255, 255, 255);
    padding: 20px 0;
    margin-bottom: 20px;
    height: 55px;
  }
  .menu-button-container {
    display: inline-flex;
    justify-content: flex-end;
    align-items: end;
    align-self: end;
    flex: 1;
    z-index: 1000;
    height: auto;
  }
  .menu-container {
    min-width: 100%;
    display: none;
    padding: 20px;
    background-color: rgb(255, 255, 255);
    position: relative;
    align-items: flex-start;
    margin-top: 20px;
    box-shadow: 1px 0px 9px 3px rgba(0, 0, 0, 0.45);
    -webkit-box-shadow: 1px 0px 9px 3px rgba(0, 0, 0, 0.45);
    -moz-box-shadow: 1px 0px 9px 3px rgba(0, 0, 0, 0.45);
  }
  .menu {
    margin: 0 auto;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    height: auto;
  }
  .nav-menu {
    min-width: 100%;
  }
  #menu-toggle:checked ~ .menu-container {
    height: auto;
    width: auto;
    display: flex;
  }
  #menu-toggle:checked ~ .menu-container .nav-menu .menu li a {
    padding: 0.5em;
    border-left: 5px solid rgb(2, 48, 71);
    opacity: 1;
    margin: auto auto 10px auto;
    transition: 0.4s;
    min-width: 100%;
    display: block;
  }
  .nav-menu .menu > li {
    display: flex;
    justify-content: start;
    margin: 0;
    width: 100%;
  }
  /*Social Links Styles*/
  .menu-social-links {
    margin: auto;
    width: auto;
    display: flex;
    justify-content: center;
  }
}
