  .menu-social-links {
    justify-content: start;
  }
}
@media (max-width: 700px) {
  .menu-social-links li {
    list-style: none;
    display: inline-block;
  }
  .menu-social-links li a {
    color: rgb(2, 48, 71);
    transition: all 0.2s ease;
    height: auto;
    padding: 0 0.625em 0 0;
    font-size: 1.4rem;
  }
